<template>
    <div>
        <div class="notice-title">{{ state.title }}</div>
        <div class="notice-time">{{ state.time }}</div>
        <div class="notice-content">{{ state.content }}</div>
    </div>
</template>

<script setup>

import { useRoute } from 'vue-router';
import { reactive, onMounted } from 'vue';

const route = useRoute()
const state = reactive({
    title: '',
    time: '',
    content: ''
})

onMounted(async () => {
    state.title = route.query.title
    state.time = route.query.time
    state.content = route.query.content
})

</script>

